@import "https://fonts.googleapis.com/css2?family=Roboto&display=swap";
body {
  margin: 0px;
  font-family: "Roboto", sans-serif;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-content: center;
}

h1 {
  color: #121212;
  font-style: normal;
  font-weight: 700;
  font-size: 42px;
  line-height: 120%;
}

.mainContainer {
  width: 1440px;
  min-height: 960px;
  background-color: #e5e5e5;
  display: flex;
  align-content: center;
}

.image {
  width: 505px;
  height: 330px;
}

.photoContainer {
  margin-left: 230px;
  margin-top: 96px;
}

.textContainer {
  margin-right: 230px;
  margin-left: 95px;
  margin-top: 136px;
}

.showPopupButton {
  background-color: #e4c1ae;
  border: none;
  color: white;
  border-radius: 32px;
  padding: 16px 40px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin-top: 10%;
  cursor: pointer;
}

.resetButton {
  background-color: #f44336;
  border: none;
  color: white;
  border-radius: 32px;
  padding: 16px 40px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  cursor: pointer;
}

.Popup {
  display: none;
  position: fixed;
  z-index: 1;
  padding-top: 100px;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.4);
}

.popup-content {
  background-color: white;
  margin: auto;
  padding: 20px 40px 20px 40px;
  width: 920px;
  height: 210px;
}

.close {
  color: #aaaaaa;
  background-color: white;
  float: right;
  font-size: 28px;
  font-weight: bold;
  text-decoration: none;
  border: none;
}
.close:hover {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}
.close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}

@media screen and (max-width: 500px) {
  .mainContainer {
    flex-direction: column;
  }
  .photoContainer {
    margin-left: 0px;
    margin-top: 0px;
    display: flex;
    justify-content: center;
    align-content: center;
  }
  .photoContainer img {
    margin-left: 0px;
    margin-top: 0px;
    width: 350px;
    height: 350px;
  }
  .textContainer {
    margin-right: 0px;
    margin-left: 0px;
    margin-top: 0px;
    width: 100%;
  }
  .textPadding {
    margin: 40px;
  }
  .popup-content {
    width: 75%;
    height: auto;
  }
}